.gridicon {
	fill: currentColor;

	&.needs-offset g {
		transform: translate( 1px, 1px );	/* translates to .5px because it's in a child element */
	}

	&.needs-offset-x g {
		transform: translate( 1px, 0 );		/* only nudges horizontally */
	}

	&.needs-offset-y g {
		transform: translate( 0, 1px );		/* only nudges vertically */
	}
}



