
// ======================================================================
// Rem function
//
// Convert px to rem in a readable fashion.
//
// Example: font-size: rem( 21px );
// ======================================================================

$root-font-size: 16px;

@function rem( $pixels, $context: $root-font-size ) {
	@return $pixels / $context * 1rem;
}



